<template>
  <div>
    <van-nav-bar
      title="会员登录"
      left-arrow
      @click-left="$router.back()"
    />
    <div class="main">
      <div class="bigtitle">手机号登录</div>
      <div class="smtitle">未注册的手机号登录后将自动注册</div>
      <div class="getnumber">
        <input type="text" placeholder="请输入手机号" v-model="userphone">
      </div>
      <div class="getnumber">
        <input type="text" placeholder="请输入图形验证码" v-model="usercode">
        <img v-if="pigimg" :src="pigimg" @click="getcodeimg">
      </div>
      <div class="getnumber">
        <input type="text" placeholder="请输入短信验证码" v-model="textcode">
        <button @click = 'getcode'>
          {{ second===sumsecond? '获取验证码' : `${second}秒后获取验证码`}}
        </button>
      </div>
      <div class="button">
        <van-button size="large" @click="login" round color="linear-gradient(to right, #ecb43b, #fe9312)">登录</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { getcodeimg, getcodetext, logincode } from '@/api/login'
import { Toast } from 'vant'
export default {
  name: 'LoginIndex',
  data () {
    return {
      pigkey: '',
      pigimg: '',
      usercode: '',
      userphone: '',
      textcode: '',
      sumsecond: 60,
      second: 60,
      settime1: null
    }
  },
  created () {
    this.getcodeimg()
  },
  methods: {
    async getcodeimg () {
      const { data: { base64, key } } = await getcodeimg()
      this.pigimg = base64
      this.pigkey = key
      // Toast('获取验证码成功')
      Toast('获图形验证码成功')
    },

    async getcode () {
      if (!this.checkcode()) {
        return
      }
      if (this.settime1 === null && this.second === this.sumsecond) {
        await getcodetext(this.usercode, this.pigkey, this.userphone)
        Toast('获取短信验证码成功 注意短信查收~')

        this.settime1 = setInterval(() => {
          this.second--
          if (this.second === 0) {
            clearInterval(this.settime1)
            this.second = this.sumsecond
            this.settime1 = null
            // console.log(this.second, this.sumsecond)
            // console.log(this.settime1)
          }
        }, 1000)
      }
    },
    // 校验方法
    checkcode () {
      if (!/^1[3-9]\d{9}$/.test(this.userphone)) {
        Toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.usercode)) {
        Toast('请输入正确的图形验证码')
        return false
      }
      return true
    },

    async login () {
      if (!this.checkcode()) {
        return
      }
      if (!/^\d{6}$/.test(this.textcode)) {
        Toast('请输入正确的验证码')
        return
      }
      const datalist = await logincode(this.textcode, this.userphone)
      this.$store.commit('user/setuserinfo', datalist.data)
      // console.log(datalist.data)
      const url = this.$route.query.backurl || '/'
      this.$router.replace(url)
      Toast('登录成功')
    }

  },
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.main{
  .bigtitle{
    margin-top: 20px;
    margin-left: 20px;
    font-size: 30px;
  }
  .smtitle{
    margin-top: 15px;
    margin-left: 20px;
    color: #c2c2c2;
    margin-bottom: 40px;
  }
  .getnumber{
    margin-top: 10px;
    margin-left: 20px;
    margin-right: 20px;
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e7e7e7;
    img{
      width: 94px;
      height: 30px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }
  input{
    border: none;
    height: 50px;
  }
  .button{
    margin-top:20px ;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}
</style>
